export default {
	data() {
		return {
			
		}
	},
	methods: {
		termsofservice(){
			uni.navigateTo({
			    url: '../termsofservice/termsofservice'
			});
		},
		back(){
			uni.switchTab({
			    url: '/pages/my/my'
			});
		},
		exit(){
			console.log(222)
			uni.showModal({
			    title: '',
			    content: '您确定退出登录吗',
			    success: function (res) {
			        if (res.confirm) {
						uni.removeStorage({ 
							key: 'userinfo', 
							success: function (res) { 
								uni.reLaunch({
								    url: '../my/my'
								});
							} ,
						});
			        } else if (res.cancel) {
						console.log(1)
			        }
			    }
			});
		}
	}
}
